import React, { useState } from "react";
import LeftSidebar from "./components/LeftSidebar";
import Main from "./components/Main";
import RightSidebar from "./components/RightSidebar";
import AddItemMenu from "./components/AddItemMenu";
import ItemDetails from "./components/ItemDetails";

import "./App.css";

const data = [
  {
    category: "Fruit and vegetables",
    items: [
      {
        name: "Avocado",
        note:
          "Nutrient-dense foods are those that provide substantial amounts of vitamins, minerals and other nutrients with relatively few calories. One-third of a medium avocado (50 g) has 80 calories and contributes nearly 20 vitamins and minerals, making it a great nutrient-dense food choice.",
        image:
          "https://i2.wp.com/www.claudiashandcraftedsoap.com/wp-content/uploads/2020/05/avocado-oil.jpg?fit=1030%2C1030&ssl=1",
      },
      { name: "Banana", note: "", image: "" },
    ],
  },
  {
    category: "Meat and Fish",
    items: [{ name: "Salmon", note: "", image: "" }],
  },
  {
    category: "Beverages",
    items: [
      { name: "Pepsi", note: "", image: "" },
      { name: "Beer", note: "", image: "" },
    ],
  },
];

function App() {
  const [route, changeRoute] = useState("default");
  const [items, newItems] = useState(data);
  const [itemDetail, setItemDetails] = useState({});
  const [searchValue, onSearch] = useState("");
  const [searchActive, setSearchActive] = useState(false);

  console.log(items);

  function changeRouteHandler(route) {
    changeRoute(route);
  }

  function addNewItems(data) {
    console.log(data);

    const { category, name, note, image } = data;
    const categoryExists = items.findIndex(
      (section) => section.category === category
    );

    console.log(categoryExists);
    if (categoryExists === -1) {
      const newarr = [{ category, items: [{ name, note, image }] }];
      console.log(newarr);
      newItems([...items, ...newarr]);
    } else if (categoryExists >= 0) {
      const itemExists = items[categoryExists].items.some(
        (item) => item.name === name
      );
      console.log(itemExists);
      if (!itemExists) {
        items[categoryExists].items.push({ name, note, image });
        console.log(items);
        newItems([...items]);
      }
    }
  }

  function itemDetailsHandler(cat, i) {
    console.log(cat, i);
    const { category } = items.find((item) => item.category === cat);
    const { name, note, image } = items
      .find((item) => item.category === category)
      .items.find((item) => item.name === i);
    const newobj = { category, name, note, image };
    setItemDetails(newobj);
    changeRoute("itemdetails");
  }

  function deleteItem(cat, i) {
    const categoryToDelete = items.find((section) => section.category === cat);
    if (categoryToDelete.items.length === 1) {
      const deleteWholeCategory = items.filter(
        (section) => section.category !== cat
      );
      newItems(deleteWholeCategory);
      changeRoute("default");
    } else if (categoryToDelete.items.length > 1) {
      const itemToNotRemove = items
        .filter((section) => section.category === cat)[0]
        .items.filter((section) => section.name !== i);
      const indexOfCategory = items.findIndex(
        (section) => section.category === cat
      );
      items[indexOfCategory].items = itemToNotRemove;
      newItems([...items]);
      changeRoute("default");
      setSearchActive(false);
    }
  }

  function filterItems(e) {
    if (e.length > 0) {
      setSearchActive(true);
    } else if (e.length === 0) {
      setSearchActive(false);
    }
    onSearch(e);
  }

  let filteredObj = data.filter((o) =>
    o.items.some((i) => i.name.toLowerCase().includes(searchValue))
  );

  if (filteredObj.length === 0) {
    filteredObj = [{ category: "", items: [] }];
  }

  const filteredItem = filteredObj[0].items.filter((i) =>
    i.name.toLowerCase().includes(searchValue)
  );

  const finalObj = [{ category: filteredObj[0].category, items: filteredItem }];
  console.log(filteredObj, filteredItem, finalObj);

  let comp;
  if (route === "default") {
    comp = <RightSidebar changeRoute={changeRouteHandler} />;
  } else if (route === "itemdetails") {
    comp = (
      <ItemDetails
        goBackHandler={() => changeRoute("default")}
        itemDetail={itemDetail}
        deleteItem={deleteItem}
      />
    );
  } else {
    comp = (
      <AddItemMenu changeRoute={changeRouteHandler} addNewItems={addNewItems} />
    );
  }

  return (
    <div className="app-container">
      <LeftSidebar />
      <Main
        filterItems={filterItems}
        items={searchActive ? finalObj : items}
        itemDetailsHandler={itemDetailsHandler}
      />
      {comp}
    </div>
  );
}

export default App;
