import React from "react";
import "./AddItemInput.css";

const AddItemInput = ({
  label,
  //placeholder,
  big,
  handleChange,
  //name,
  ...otherProps
}) => {
  console.log(otherProps);
  return (
    <div className="additeminput-container">
      <p>{label}</p>
      <input
        //name={name}
        onChange={handleChange}
        className={big ? "big" : null}
        type="text"
        //placeholder={placeholder}
        {...otherProps}
      ></input>
    </div>
  );
};
export default AddItemInput;
