import React from "react";
import "./ItemDetails.css";
import Button from "./Button";
import { ReactComponent as Arrow } from "../assets/arrow.svg";

const ItemDetails = ({ itemDetail, goBackHandler, deleteItem }) => {
  const { category, name, note, image } = itemDetail;
  return (
    <div className="itemdetails">
      <div onClick={goBackHandler} className="back-container">
        <Arrow className="arrow-icon" />
        <p className="back-paragraph">back</p>
      </div>
      {image && (
        <div
          style={{ backgroundImage: `url(${image})` }}
          className="item-image"
        ></div>
      )}
      <p className="itemdetail-titles">name</p>
      <p className="itemdetail-paragraph">{name}</p>
      <p className="itemdetail-titles">category</p>
      <p className="itemdetail-paragraph">{category}</p>
      {note && (
        <div>
          <p className="itemdetail-titles">note</p>
          <p className="itemdetail-paragraph">{note}</p>
        </div>
      )}
      <div className="buttons-container">
        <Button
          buttonClickHandler={() => deleteItem(category, name)}
          text="delete"
        />
        <Button
          //buttonClickHandler={buttonClickHandler}
          yellow
          text="Add to list"
        />
      </div>
    </div>
  );
};
export default ItemDetails;
