import React from "react";
import "./ItemsCategory.css";

const ItemsCategory = ({ category, items, itemDetailsHandler }) => {
  console.log(category, items);

  return (
    <div className="items-category">
      <h1>{category}</h1>
      <div className="items-list">
        {items.map((item, i) => (
          <p
            onClick={() => itemDetailsHandler(category, item.name)}
            key={i}
            className="item"
          >
            {item.name}
          </p>
        ))}
      </div>
    </div>
  );
};

export default ItemsCategory;
