import React, { useState } from "react";
import "./AddItemMenu.css";
import AddItemInput from "./AddItemInput.js";
import Button from "./Button";

const AddItemMenu = ({ changeRoute, addNewItems }) => {
  const [item, addItem] = useState({
    name: "",
    note: "",
    image: "",
    category: "",
  });

  function handleChange(event) {
    const { name, value } = event.target;

    addItem({ ...item, [name]: value });
  }

  function changeRouteHandler() {
    changeRoute("default");
  }

  function handleSubmit(e) {
    e.preventDefault();
    addNewItems(item);
  }

  return (
    <div className="additemmenu-container">
      <h1>Add a new item</h1>
      <form onSubmit={handleSubmit}>
        <AddItemInput
          name="name"
          handleChange={handleChange}
          label="Name"
          placeholder="Enter a name"
          required
        />
        <AddItemInput
          name="note"
          handleChange={handleChange}
          label="Note (optional)"
          placeholder="Enter a note"
          big
        />
        <AddItemInput
          name="image"
          handleChange={handleChange}
          label="Image (optional)"
          placeholder="Enter a url"
        />
        <AddItemInput
          name="category"
          handleChange={handleChange}
          label="Category"
          placeholder="Enter a category"
          required
        />
        <div className="buttons-container">
          <Button
            type="button"
            buttonClickHandler={changeRouteHandler}
            text="cancel"
          />
          <Button type="submit" text="Save" yellow />
        </div>
      </form>
    </div>
  );
};
export default AddItemMenu;
