const Items = ({ data, itemDetailsHandler }) => {
  return (
    <div className="items-container">
      {data.length > 0 &&
        data.map((item, i) => {
          console.log(item);
          return (
            <ItemsCategory
              itemDetailsHandler={itemDetailsHandler}
              key={i}
              {...item}
            />
          );
        })}
    </div>
  );
};
export default Items;
